
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import { ossPrefix } from './../config'
export default {
components: {
Flexbox,
FlexboxItem,
XHeader
},
data () {
return {
imgUrl:'',
}
},
mounted () {
if (this.$route.query.brand == 1) {
this.imgUrl = `${ossPrefix}nh.jpg`
} else {
this.imgUrl = `${ossPrefix}dyc.jpg`
}
}
}
